<template>
  <div class="musicPlayer">
    <audio
      ref="audio"
      :src="questionAudio"
      :name="12"
      @ended="audioEnd"
      controls
      class="audio"
    ></audio>
  </div>
</template>
<script>
export default {
  name: "musicPlayer",
  props: ["questionAudio"],
  data() {
    return {
      icon: false,
    };
  },
  methods: {
    handleplay() {
      this.icon = !this.icon;
      setTimeout(() => {
        this.$refs.audio.play();
      }, 100);
    },
    audioEnd() {
      this.icon = !this.icon;
      setTimeout(() => {
        this.$refs.audio.pause();
      }, 100);
      this.$emit("stopMusic");
    },
  },
};
</script>
<style lang="less" scoped>
.musicPlayer {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>
