<template>
  <div class="musiclist">
    <div>
      <Nav class="first">歌曲列表</Nav>
    </div>
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      class="list"
    >
      <van-cell v-for="(item, index) in tracks" :key="index" class="cell">
        <span class="num">{{ index < 10 ? "0" + index : index }}</span>
        <div class="name" v-if="index == 0">
          <span> {{ item.name }} </span>
        </div>
        <div class="name" v-else>
          <span> {{ item.name }} </span>
          <van-icon
            name="play"
            v-if="!item.isPlay"
            @click="playMusic(item.id, index)"
          />
          <van-icon name="pause" v-else @click="stopMusic(index)" />
        </div>
        <span class="singer"> {{ item.ar[0].name }} </span>
      </van-cell>
    </van-list>
    <footer>
      <music-player ref="music" :questionAudio="musicUrl"></music-player>
    </footer>
  </div>
</template>
<script>
import musicPlayer from "../../components/musicPlayer.vue";
import Nav from "../../components/Nav.vue";

export default {
  components: { musicPlayer, Nav },
  data() {
    return {
      loading: false,
      finished: false,
      tracks: [],
      musicUrl: "https://static.xfanread.com/annualReview/annualReview2019.mp3",
    };
  },
  mounted() {
    this.getDetailMusicList();
  },
  methods: {
    async playMusic(musicId, index) {
      // dev env
      // let { data, code } = await axios.get(
      //   `http://localhost:8080/api2/song/url?id=` + musicId
      // );

      // formal env
      let { data, code } = await axios.get(
        `http://49.232.78.210:3000/song/url?id=` + musicId
      );
      if (code == 200) {
        this.musicUrl = data[0].url;
        this.tracks[index].isPlay = true;
        this.$refs.music.handleplay();
      }
    },
    stopMusic(index) {
      this.tracks[index].isPlay = false;
      this.$refs.music.audioEnd();
    },
    async getDetailMusicList() {
      let musicListId = this.$route.query.Id;
      // dev env
      // let { code, playlist } = await axios.get(
      //   `http://localhost:8080/api2/playlist/detail?id=` + musicListId
      // );

      // formal env
      let { code, playlist } = await axios.get(
        `http://49.232.78.210:3000/playlist/detail?id=` + musicListId
      );
      if (code == 200) {
        this.tracks = playlist.tracks;
        this.tracks.unshift({ name: "歌名", ar: [{ name: "歌手" }] });
        this.tracks = this.tracks.map((item) => {
          return { ...item, isPlay: false };
        });
      } else {
        this.$Message("发生错误");
      }
    },
  },
};
</script>
<style lang="less" scoped>
footer {
  background-color: #ffffff;
  position: fixed;
  bottom: 0;
  width: 100%;
  z-index: 99;
}
.musiclist {
  background-color: #fafafa;
  overflow: auto;
}
.van-cell__value {
  display: flex;
  // justify-content: flex-start;

  .num,
  .name,
  .singer {
    margin-right: 20px;
    color: #a692bd;
    flex: 0.6;
  }
  .num {
    padding-left: 10px;
    flex: 0.2;
  }
  .singer {
    flex: 0.5;
  }
}
.cell {
  font-size: 17px;
  background-color: #ffffff;
  padding: 15px;

  .name {
    color: #633b91;
  }
}
.list {
  margin-top: 70px;
}
.first {
  position: fixed;
  top: 0;
  z-index: 100;
}
</style>
